<template>
  <div>
    <!-- 面包屑区域 -->
    <el-breadcrumb>
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>工具中心</el-breadcrumb-item>
      <el-breadcrumb-item>圈外工具</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片区域 -->
    <el-card>
      <el-row>
        <el-col
          style="margin-top:60px;margin-left:4%;margin-right:4%;"
          :span="2"
          :key="item.id"
          v-for="item in utilsList"
        >
          <div @click="openDialog(item.path)" style="cursor:pointer">
            <el-tooltip class="item" :content="item.utilsDescribe" placement="top">
              <!-- <el-card style="background-color:#479DE3;height:80px;border-radius: 15px;">
              <div style="text-align: center;font-size:25px;font-color:while">
                <span>{{item.utilsName}}</span>
              </div>
              </el-card>-->
              <el-button type="primary">{{item.utilsName}}</el-button>
            </el-tooltip>
          </div>
        </el-col>
      </el-row>
    </el-card>
    <el-dialog
      title="快速开课"
      :visible.sync="openLessonDialog"
      width="90%"
      :before-close="closeOpenLessonDialog"
      :close-on-click-modal="false"
    >
      <span>该操作只生成对应的课程包的排期的学籍，不会生成对应的订单号，如果需要其他，请走正式流程</span>
      <br />
      <br />
      <br />
      <el-form
        :inline="true"
        :model="openLessonInfo"
        class="demo-form-inline"
        ref="openLessonInfoRef"
        :rules="openLessonRules"
      >
        <el-form-item label="passport_id" prop="passportId">
          <el-input v-model="openLessonInfo.passportId" placeholder="用户的passport_id"></el-input>
        </el-form-item>

        <el-form-item label="课程包" prop="lessonPackageId">
          <el-select
            v-model="openLessonInfo.lessonPackageId"
            border
            filterable
            placeholder="请选择"
            clearable
            @change="selectLessonPackage()"
            style="width:440px"
          >
            <el-option
              v-for="item in lessonPackageList"
              :key="item.value"
              :label="item.name + ' [code:' + item.code + ']'"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="排期" prop="termScheduleId">
          <el-select
            v-model="openLessonInfo.termScheduleId"
            filterable
            placeholder="请选择"
            clearable
            style="width:360px"
          >
            <el-option
              v-for="item in termScheduleList"
              :key="item.value"
              :label="item.term + '期，排期状态：' + item.termStatusDesc +'，[code:' + item.code + ']' "
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <!-- 具体内容 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeOpenLessonDialog()">取 消</el-button>
        <el-button type="primary" @click="openLesson()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 重置课程 -->
    <el-dialog
      title="课程初始化"
      :visible.sync="resetLessonDialog"
      width="90%"
      :before-close="closeResetLessonDialog"
      :close-on-click-modal="false"
    >
      <span>此操作会重置学籍状态，并将所有的学习记录清空</span>
      <br />
      <br />
      <br />
      <el-form
        :inline="true"
        :model="resetLessonInfo"
        class="demo-form-inline"
        ref="resetLessonPassportIdRef"
        :rules="resetLessonRules"
      >
        <el-form-item label="passport_id" prop="passportId">
          <el-input v-model="resetLessonInfo.passportId" placeholder="用户的passport_id"></el-input>
        </el-form-item>
        <el-button type="primary" @click="getUserLesson()">搜 索</el-button>
      </el-form>
      <el-table :data="usesLessonInfo" border style="width: 100%">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-table :data="props.row.lessonList" height="500" border style="width: 100%">
              <el-table-column prop="name" label="课程名称" width="180"></el-table-column>
              <el-table-column prop="lessonTypeName" label="课程类型" width="180"></el-table-column>
              <el-table-column prop="lessonCode" label="lessonCode"></el-table-column>
              <el-table-column prop="finishRuleName" label="完课规则"></el-table-column>
              <el-table-column prop='lessonCode' label="操作">
                <template slot-scope="lesson">
                <el-button type="primary" @click="restLessonConfirm(props.row.termScheduleCode,lesson.row.lessonCode,lesson.row.name)"
                 size="small">重置</el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column prop="lessonPackageName" label="课程包" width="300"></el-table-column>
        <el-table-column prop="term" label="排期数" width="100"></el-table-column>
        <el-table-column label="操作">点击左侧箭头可选择对该课程包下的课程进行重置（仅限此passport_id用户）</el-table-column>
      </el-table>
      <!-- 具体内容 -->
    </el-dialog>
  </div>
</template>
<script>
import componentVue from '../caseConfig/component.vue'
export default {
  data () {
    return {
      projectList: [],
      utilsList: [],
      fenye: {
        pageSize: 10,
        pageNum: 1
      },
      openLessonDialog: false,
      openLessonInfo: {
        passportId: '',
        lessonPackageId: '',
        termScheduleId: ''
      },
      lessonPackageList: [],
      termScheduleList: [],
      UserLessonInfo: [],
      resetLessonDialog: false,
      resetLessonInfo: {
        passportId: '',
        termScheduleCode: '',
        lessonCode: ''
      },
      usesLessonInfo: [],
      openLessonRules: {
        passportId: [
          { required: true, message: '请输入passportId', trigger: 'blur' }
        ],
        lessonPackageId: [
          { required: true, message: '请选择课程包', trigger: 'change' }
        ],
        termScheduleId: [
          { required: true, message: '请选择排期', trigger: 'change' }
        ]
      },
      resetLessonRules: {
        passportId: [
          { required: true, message: '请输入passportId', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    async test () {
      const response = await this.$common.get('/userInfo/test')
      console.log(response)
    },
    // 关闭重置课程弹窗
    closeResetLessonDialog () {
      this.resetLessonDialog = false
      this.usesLessonInfo = []
      this.$refs.resetLessonPassportIdRef.resetFields()
    },
    // 重置课程
    async restLessonConfirm (termScheduleCode, lessonCode, lessonName) {
      await this.$confirm('确定重置[' + this.resetLessonInfo.passportId + ']的【' + lessonName + '】课程么?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.restLesson(termScheduleCode, lessonCode)
      })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消重置'
          })
        })
    },
    // 重置课程的请求
    async restLesson (termScheduleCode, lessonCode) {
      this.resetLessonInfo.termScheduleCode = termScheduleCode
      this.resetLessonInfo.lessonCode = lessonCode
      const response = await this.$common.get('/classMemberRegistration/resetLesson', this.resetLessonInfo)
      if (response.code == 10000) {
        this.$message.success(response.msg)
      } else {
        this.$message.error(response.msg)
      }
    },
    // 获取工具列表
    async getAllUtils () {
      const response = await this.$common.get(
        '/quanwaiUtils/getAllUtils',
        this.fenye
      )
      if (response.code === 10000) {
        this.utilsList = response.data
      } else {
        this.$message.error(response.msg)
      }
    },
    // 根据工具的path判断打开那个dialog
    openDialog (path) {
      if (path == 'openLesson') {
        this.goOpenLessonDialog()
      } else if (path == 'resetLesson') {
        this.goResetLessonDialog()
      } else if (path == 'syncAccessToken') {
        this.goSyncAccessToken()
      }
    },
    // 打开开课dialog
    async goOpenLessonDialog () {
      this.openLessonDialog = true
      const response = await this.$common.get('/lessonPackage/getAllPackage')
      this.lessonPackageList = response.data
    },
    // 打开初始化课程dialog
    goResetLessonDialog () {
      this.resetLessonDialog = true
    },
    // 获取用户的有效学籍的课程信息
    async getUserLesson () {
      const response = await this.$common.get(
        '/classMemberRegistration/getUserLessonByPassportId?passportId=' +
          this.resetLessonInfo.passportId
      )
      this.usesLessonInfo = response.data
    },
    // 关闭开课dialog
    closeOpenLessonDialog () {
      this.openLessonDialog = false
      this.$refs.openLessonInfoRef.resetFields()
    },
    // 选中课程包，查排期
    async selectLessonPackage () {
      // 选中了，然后去查询对应课程包的排期（有效）
      const response = await this.$common.get(
        '/termSchedule/getTermSchedule?lessonPackageId=' +
          this.openLessonInfo.lessonPackageId
      )
      this.termScheduleList = response.data
    },
    // 开课请求
    async openLesson () {
      await this.$refs.openLessonInfoRef.validate((valid) => {
        if (valid) {
          const response = this.$common.post(
            '/classMemberRegistration/openLesson',
            this.openLessonInfo
          )
          if (response.code == 10000) {
            this.closeOpenLessonDialog()
            this.$message.success(response.msg)
          }
        } else {
          return false
        }
      })
    },
    // 重置表单
    clearDialog () {
      console.log('关闭')
    },
    // 同步access_token
    async goSyncAccessToken () {
      await this.$confirm('同步线上的access_token到测试环境？除非紧急情况，否则不要轻易同步！！！', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.syncAccessToken()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消同步'
          })
        })
    },
    // 同步token请求
    async syncAccessToken () {
      const response = await this.$common.get('/accessToken/sync')
      if (response.code == 10000) {
        this.$message.success(response.msg)
      } else {
        this.$message.error(response.msg)
      }
    }
  },

  created () {
    // 初始化列表
    this.getAllUtils()
  }
}
</script>
<style lang="less" scoped></style>
